/* 
url
.....?id=12456&name=xiaolongbao 
{id:1245,name:"xiaolongbao"}
*/

export default function () {
    var urlString = window.location.search;
    var obj = {};
    let reg = /[?&][^?&]+\=[^?&]+/g;
    var arr = urlString.match(reg);
    arr.forEach((item) => {
        // substring(index)  index index 
        let tempArr = item.substring(1).split("=");
        console.log(tempArr);
        // encodeURIComponent decodeURIComponent URI # /   decodeURI
        let key = decodeURIComponent(tempArr[0]);
        let val = decodeURIComponent(tempArr[1]);
        obj[key] = val;
    });
    console.log(urlString.match(reg));//["?id=123445","&name=xiaoming"]
    console.log(obj);

    return obj;
}